.header {
  width: 100%;
  height: 50rem;
  background-image: url("/build/img/hero.jpg");
  background-repeat: no-repeat;
  background-size: 200%;
  background-position: top;
  color: #FFF;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    background-size: 100%;
    background-position: center;
    height: 45rem;
    position: relative;
  }
}
.header .title h1 {
  padding-top: 2rem;
  font-size: 3rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .header .title h1 {
    font-size: 4rem;
  }
}
.header .title h1 span {
  font-weight: 900;
}
.header .telefono {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .header .telefono {
    padding-top: 4rem;
  }
}
.header .telefono picture img {
  margin-top: 1rem;
  width: 2rem;
  height: 2rem;
}
.header .telefono span {
  padding: 0.4rem;
}

@media (min-width: 768px) {
  .barra {
    width: 93%;
    padding: 0 15rem;
  }
}
@media (min-width: 768px) {
  .barra .titleTelefono {
    display: flex;
    justify-content: space-between;
    height: 8rem;
  }
}

.navegacion {
  text-align: center;
  margin-bottom: 7rem;
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    margin-left: 45rem;
  }
}
.navegacion a {
  margin-left: 4rem;
  font-weight: 700;
  display: inline-block;
  width: 10rem;
}
@media (min-width: 768px) {
  .navegacion a {
    margin: 0;
    width: 10rem;
  }
}
.navegacion a:hover {
  background: #343A40;
}

.eslogan {
  text-align: center;
}
@media (min-width: 768px) {
  .eslogan {
    position: absolute;
    top: 78%;
    left: 55%;
  }
}
.eslogan h2 {
  font-size: 2rem;
  margin: 0;
}
@media (min-width: 768px) {
  .eslogan h2 {
    font-size: 4rem;
  }
}
.eslogan p {
  margin: 0;
}
@media (min-width: 768px) {
  .eslogan p {
    text-align: right;
  }
}

